<template>
  <ve-pie :data="chartData" width="80%" style="margin: auto;"></ve-pie>
</template>

<script>
export default {
  name: 'profit',
  data () {
    return {
      chartData: {
        columns: ['Hand', 'Gain'],
        rows: [
          {
            'Hand':1,
            'Gain':1
          },
          {
            'Hand':1,
            'Gain':1
          },

        ]
      }
    }
  },
  methods:{
    profit() {
        this.$http.get('http://'+this.$root.backIP+'/analysis')
          .then( (response) => {
            response.data.forEach(element => {
              this.chartData.rows.push(element)
            });
          })
    },
  },
  mounted(){
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


